import { setAttribute, setComponentprops, createComponent } from './index'
export function diff(dom, vnode, container) {
    const ret = diffNode(dom, vnode);
    if (container) {
        container.appendChild(ret)
    }
    // return ret
}

export function diffNode(dom, vnode) {
    let out = dom
    if (vnode === undefined || vnode === null || typeof vnode === 'bool') {
        vnode = ''
    }

    if (typeof vnode === 'number') {
        vnode = String(vnode)
    }
    // å¦‚æœnode æ˜¯å­—ç¬¦ä¸²
    if (typeof vnode === 'string') {
        if (dom && dom.nodeType === 3) {
            if (dom.textContent !== vnode) {
                // æ›´æ–°æ–‡æœ¬çš„å†…å®¹
                dom.textContent = vnode
            }
        } else {
            out = document.createTextNode(vnode)
            if (dom && dom.parentNode) {
                dom.parentNode.replaceNode(out, dom)
            }
        }
        return out
    }

    if (typeof vnode.tag === 'function') {
        return diffComponent(out, vnode)
    }



    // éæ–‡æœ¬çš„domèŠ‚ç‚¹
    if (!dom) {
        out = document.createElement(vnode.tag)
    }
    // æ¯”è¾ƒå­èŠ‚ç‚¹(domèŠ‚ç‚¹ å’Œç»„ä»¶)
    if (vnode.childrens && vnode.childrens.length > 0 || (out.childNodes && out.childNodes.length > 0)) {
        // å¯¹æ¯”ç»„ä»¶ æˆ–è€…å­èŠ‚ç‚¹
        diffChildren(out, vnode.childrens)
    }
    // æ¯”è¾ƒå½“å‰èŠ‚ç‚¹çš„å±æ€§
    diffAttribute(out, vnode)
    return out
}

function diffChildren(dom, vchildren) {
    const domChildren = dom.childNodes;
    const children = [];
    const keyed = {}
        // å°†æœ‰keyçš„èŠ‚ç‚¹(ç”¨å¯¹è±¡ä¿å­˜ hashè¡¨) å’Œæ²¡æœ‰key çš„èŠ‚ç‚¹ï¼ˆç”¨æ•°ç»„ä¿å­˜ï¼‰ åˆ†å¼€
    if (domChildren.length > 0) {
        domChildren.forEach(item => {
            children.push(item)
                // if (item.attributes !== undefined) {
                //     if ([...item.attributes].find(item => item.name === 'key') !== undefined) {
                //         keyed[item.attributes['key']] = item
                //     } else {
                //         children.push(item)
                //     }
                // }
        })
    }
    if (vchildren && vchildren.length > 0) {
        let min = 0;
        let childrenLen = children.length;
        [...vchildren].forEach((vchild, i) => {
            // è·å–è™šæ‹ŸèŠ‚ç‚¹ä¸­æ‰€æœ‰çš„key
            const key = vchild.key
            let child;
            if (key) {
                // å¦‚æœæœ‰key,æ‰¾åˆ°å¯¹åº”keyå€¼å¾—èŠ‚ç‚¹
                if (keyed[key]) {
                    child = keyed[key];
                    keyed[key] = undefined;
                }
            } else if (childrenLen > min) {
                // å¦‚æœæ²¡æœ‰keyï¼Œåˆ™ä¼˜å…ˆæŸ¥æ‰¾ç±»å‹ç›¸åŒçš„èŠ‚ç‚¹
                for (let j = min; j < childrenLen; j++) {
                    let c = children[j]
                    if (c) {
                        child = c;
                        children[j] = undefined
                        if (j === childrenLen - 1) {
                            childrenLen--
                        }
                        if (j === min) {
                            min++
                        }
                        break;
                    }
                }
            }
            // å¯¹æ¯”
            child = diffNode(child, vchild);
            const f = domChildren[i]
            if (child && child !== dom && child !== f) {
                // å¦‚æœæ›´æ–°å‰çš„å¯¹åº”ä½ç½®ä¸ºç©º,è¯´æ˜æ­¤èŠ‚ç‚¹æ˜¯æ–°å¢çš„
                if (!f) {
                    dom.appendChild(child)
                } else if (child === f.nextSibling) {
                    removeNode(f);
                } else {
                    dom.insertBefore(child, f)
                }
            }
        })
    }
}

function diffComponent(dom, vnode) {
    let comp = dom;
    // å¦‚æœç»„ä»¶æ²¡æœ‰å˜åŒ–
    if (comp && comp.constructor === vnode.tag) {
        // é‡æ–°è®¾ç½®å±æ€§
        setComponentprops(comp, vnode.attrs)
            // å¤åˆ¶
        dom = comp.base
    } else {
        // ç»„ä»¶ç±»å‹å‘ç”Ÿå˜åŒ–
        if (comp) {
            // å…ˆç§»å‡ºæ—§çš„ç»„ä»¶
            unmountComonent(comp)
            comp = null
        } else {
            //1. åˆ›å»ºæ–°çš„ç»„ä»¶
            comp = createComponent(vnode.tag, vnode.attrs)
                //2.è®¾ç½®åˆ›å»ºçš„ç»„ä»¶çš„å±æ€§
            setComponentprops(comp, vnode.attrs)
                //3. ç»™å½“å‰ç»„ä»¶æŒ‚åœ¨base
            dom = comp.base
        }
    }
    return dom
}

function unmountComonent(comp) {
    removeNode(comp.base)
}

function removeNode(dom) {
    if (dom && dom.parentNode) {
        dom.parentNod.removeNode(dom)
    }
}



function diffAttribute(dom, vnode) {
    const oldAttrs = {}
    const newAttrs = vnode.attrs
        // domæ˜¯å·²ç»æ¸²æŸ“äº†  åŸæœ‰çš„èŠ‚ç‚¹  vnode æ˜¯æ–°çš„èŠ‚ç‚¹
    const domAttrs = dom.attributes;
    [...domAttrs].forEach(item => {
            oldAttrs[item.name] = item.value
        })
        // æ¯”è¾ƒ å±æ€§
        // å¦‚æœåŸæ¥å±æ€§è·Ÿæ–°çš„å±æ€§å¯¹æ¯”ï¼Œä¸åœ¨æ–°çš„å±æ€§ä¸­ï¼Œåˆ™å°†å…¶ç§»å‡º (å±æ€§å€¼è®¾ç½®ä¸ºundefined)
    for (let key in oldAttrs) {
        if (!(key in newAttrs)) {
            setAttribute(dom, key, undefined)
        }
    }
    for (let key in newAttrs) {
        //å€¼ä¸åŒ å°±æ›´æ–°å±æ€§çš„å€¼
        if (oldAttrs[key] !== newAttrs[key]) {
            setAttribute(dom, key, newAttrs[key])
        }
    }
    /// æ›´æ–°å±æ€§çš„å€¼
}