function createComponent(comp, props) {
    // å¦‚æœæ˜¯ç±»å®šä¹‰çš„ç»„ä»¶ åˆ™åˆ›å»ºå®ä¾‹ è¿”å›
    let inst;
    if (comp.prototype && comp.prototype.render) {
        inst = new comp(props)
    } else {
        // å¦‚æœæ˜¯å‡½æ•°ç»„ä»¶ï¼Œå°†å‡½æ•°ç»„ä»¶æ‰©å±•æˆç±»ç»„ä»¶ æ–¹ä¾¿åé¢ç»Ÿä¸€ç®¡ç†
        inst = new Componnent(props)
        inst.constructor = comp
            // ç»™å½“å‰å®ä¾‹å®šåˆ¶render å‡½æ•°
        inst.render = function() {
            return this.constructor(props)
        }
    }
    return inst
}

function setComponentprops(comp, props) {
    // å¤„ç†é’©å­å‡½æ•°
    if (!comp.base) {
        if (comp.componentWillMount) {
            comp.componentWillMount()
        }
    } else if (comp.componentWillReceiveProps) {
        comp.componentWillReceiveProps()
    }

    // è®¾ç½®ç»„ä»¶çš„å±æ€§
    comp.props = props
        // æ¸²æŸ“ç»„ä»¶
    renderComponent(comp)
}

function renderComponent(comp) {
    let base;
    const render = comp.render()
    base = diffNode(comp.base, render)
    if (comp.base && comp.componentWillUpdate) {
        comp.componentWillUpdate()
    }
    if (comp.base) {
        if (comp.componentDidUpdate) {
            comp.componentDidUpdate()
        }
    } else if (comp.componentDidMount) {
        comp.componentDidMount()
    }
    comp.base = base
}




// è®¾ç½®å±æ€§
export function setAttribute(dom, key, value) {
    //å°†å±æ€§åclassName è½¬æ¢ä¸ºclass
    if (key === 'className') {
        key = 'class'
    }

    // å¦‚æœæ˜¯äº‹ä»¶
    if (/on\w+/.test(key)) {
        // è½¬å°å†™
        dom[key.toLowerCase()] = value
    } else if (key === 'style') {
        if (!value || typeof value === 'string') {
            dom.style.cssText = value || '';
        } else if (value && typeof value === 'object') {
            for (let key in value) {
                if (typeof value[key] === 'number') {
                    dom.style[key] = value[key] + 'px'
                } else {
                    dom.style[key] = value[key]
                }
            }
        }
    } else {
        if (key in dom) {
            dom[key] = value || ''
        }
        if (value) {
            dom.setAttribute(key, value)
        } else {
            dom.removeAttribute(key, value)
        }
    }
}
export { renderComponent, setComponentprops, createComponent }
export default ReactDom